<template>
  <div class="CodeView">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span style="font-size:16px">代码检视列表</span>
        <el-button
          type="text"
          class="btn btn-primary"
          style="float:right"
          @click.stop="NewCodeView"
        >新建检视</el-button>
        <el-dialog
          title="新建检视"
          :visible.sync="dialogFormVisible"
          :close-on-click-modal="false"
          top="0vh"
        >
          <el-form :model="form" :rules="rules" ref="form">
            <el-form-item label="项目名称" label-width="120px" prop="name">
              <el-input v-model="form.name" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="缺陷类型" label-width="120px" prop="bugtype">
              <el-select v-model="form.bugtype" placeholder="请选择">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="缺陷等级" label-width="120px" prop="buglevel">
              <el-select v-model="form.buglevel" placeholder="请选择">
                <el-option
                  v-for="item in options2"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="源文件路径" label-width="120px" prop="sourcefile">
              <el-input v-model="form.sourcefile" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="开始行" label-width="120px" prop="beginline">
              <el-col :span="10">
                <el-input v-model="form.beginline" autocomplete="off"></el-input>
              </el-col>
              <el-col :span="4" style="text-align:right">结束行：</el-col>
              <el-col :span="10">
                <el-form-item prop="endline">
                  <el-input v-model="form.endline" autocomplete="off"></el-input>
                </el-form-item>
              </el-col>
            </el-form-item>
            <el-form-item label="版本信息" label-width="120px" prop="version">
              <el-input v-model="form.version" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="检视人" label-width="120px" prop="checkuser">
              <el-input v-model="form.checkuser" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="检视时间" label-width="120px">
              <el-col :span="11">
                <el-form-item prop="checktime">
                  <el-date-picker
                    type="date"
                    placeholder="选择日期"
                    v-model="form.checktime"
                    style="width: 100%;"
                  ></el-date-picker>
                </el-form-item>
              </el-col>
            </el-form-item>
            <el-form-item label="修改人" label-width="120px" prop="edituser">
              <el-input v-model="form.edituser" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="修改建议" label-width="120px" prop="editsuggest">
              <el-input v-model="form.editsuggest" autocomplete="off" type="textarea" autosize></el-input>
            </el-form-item>
            <el-form-item label="备注" label-width="120px">
              <el-input v-model="form.remark" autocomplete="off" type="textarea" autosize></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit('form')" style="margin-left:60px">保存</el-button>
              <el-button type="primary" @click="resetForm('form')" style="margin-left:60px">重置</el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
      </div>
      <el-table :data="tableData" stripe style="width: 100%" :cell-style="{padding:0}">
        <el-table-column prop="ID" label="编码" width="60"></el-table-column>
        <el-table-column prop="projectid" label="项目编码" width="125"></el-table-column>
        <el-table-column prop="name" label="项目名称" width="100"></el-table-column>
        <el-table-column prop="taskid" label="任务编号" width="100"></el-table-column>
        <el-table-column prop="bugid" label="缺陷编号" width="100"></el-table-column>
        <el-table-column prop="bugtype" label="缺陷类型" width="140"></el-table-column>
        <el-table-column prop="buglevel" label="缺陷等级" width="70"></el-table-column>
        <el-table-column prop="sourcefile" label="源文件路径" width="180"></el-table-column>
        <el-table-column prop="beginline" label="开始行" width="70"></el-table-column>
        <el-table-column prop="endline" label="结束行" width="70"></el-table-column>
        <el-table-column prop="version" label="版本信息" width="80"></el-table-column>
        <el-table-column prop="checkuser" label="检视人" width="80"></el-table-column>
        <el-table-column prop="checktime" label="检视时间" width="100"></el-table-column>
        <el-table-column prop="edituser" label="修改人" width="80"></el-table-column>
        <el-table-column prop="editsuggest" label="修改建议" width="180"></el-table-column>
        <el-table-column prop="remark" label="备注" width="180"></el-table-column>
        <el-table-column label="操作" width="120">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark" content="修改" placement="top-start">
              <el-button
                @click="modifyMsg(scope.row)"
                type="primary"
                size="mini"
                class="el-icon-edit"
              ></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="删除" placement="top-start">
              <el-button
                @click="deleteMsg(scope.row)"
                type="danger"
                size="mini"
                class="el-icon-delete"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <CutPage v-on:currentPage="currentPage" style="margin-top:20px"></CutPage>
    </el-card>
  </div>
</template>

