          { required: true, message: "请输入检视时间", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    deleteMsg(row) {
      this.$confirm("确认删除？")
        .then(_ => {
          for (let i = 0; i < this.tableData.length; i++) {
            if (this.tableData[i].ID == row.ID) {
              this.tableData.splice(i, 1);
            }
          }
        })
        .catch(_ => {});
    },
    currentPage(value) {
      console.log(value);
    },
    NewCodeView() {
      this.dialogFormVisible = true;
    },
    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert("submit!");
          this.$router.push("/Main/CodeView");
          this.$message({
            message: "恭喜你，创建成功",
            type: "success"
          });
          this.dialogFormVisible = false;
        } else {
          console.log("error submit!!");
          return false;
        }
      });
      console.log(this.form);
    },
    modifyMsg(row) {
      console.log(row);
      this.dialogFormVisible = true;
      this.form = row;
    },
    resetForm(formName) {
      this.form={}
    }
  }
};
</script>
